import React, { useState, useEffect, useCallback } from 'react';
import type { FC, ReactNode } from 'react';
import { EditWrapper } from './style';
import { interviewTime } from '@/type';
import InterviewItem from '../interview-item';
import { Button, Form, Input, FloatButton, Empty } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import { getInterviewTimeDirec, getPreRecruitTime } from '@/service/api';
import { useParams } from 'react-router-dom';

interface LayoutEditProps {
  children?: ReactNode;
}

const LayoutEdit: FC<LayoutEditProps> = () => {
  const [infoData, setInfoData] = useState<interviewTime[]>([]);
  const [preTime, setPreTime] = useState(0);
  const params = useParams();
  // 新增面试时间
  const addTime = useCallback(() => {
    const date = new Date();
    setInfoData([
      ...infoData,
      {
        id: date.getTime(),
        startTime: date.toLocaleString(),
        endTime: date.toLocaleString(),
        quota: '',
        location: '',
        spareQuota: 0,
        isdefalut: true,
        direction: Number(params.direction)
      }
    ]);
    // 滑动到底部;
    setTimeout(() => {
      window.scrollTo(0, document.documentElement.scrollHeight);
    }, 50);
  }, [infoData]);

  // 更改面试前预约的时间
  const changeRecruitTime = (value: { time: string }) => {
    console.log(value);
  };

  // 获取面试数据
  useEffect(() => {
    getInterviewTimeDirec(Number(params.direction)).then((res) => {
      setInfoData(res.data);
    });

    getPreRecruitTime().then((res) => {
      res.code === 200 && setPreTime(res.data);
    });
  }, [params]);

  return (
    <EditWrapper>
      <div className="items-header">
        <Form name="timeForm" onFinish={changeRecruitTime} className="timeForm">
          <Form.Item
            label="面试开始前多久能预约（单位：分钟）"
            name="time"
            // initialValue={preTime}
          >
            <Input type="number" value={preTime} />
          </Form.Item>
          <Form.Item>
            <Button htmlType="submit" type="primary">
              提交
            </Button>
          </Form.Item>
        </Form>
      </div>
      <div className="items-area">
        {infoData.length !== 0 ? (
          infoData.map((item) => {
            return <InterviewItem infoData={item} key={item.id} />;
          })
        ) : (
          <div className="emptyArea">
            <Empty
              imageStyle={{ height: 120 }}
              description={
                <span style={{ color: '#94979e' }}>什么都没有~</span>
              }
            >
              <Button type="primary" onClick={addTime}>
                添加面试时间
              </Button>
            </Empty>
          </div>
        )}
      </div>

      <div className="items-tips">
        <FloatButton
          type="primary"
          style={{ right: 24 }}
          icon={<PlusOutlined />}
          tooltip={<div>新增面试时间</div>}
          onClick={addTime}
        />
      </div>
    </EditWrapper>
  );
};

export default LayoutEdit;
